@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style-type: none;
}

// Fonts
a,
p,
span,
button,
details,
summary,
label,
input,
td,
th,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: var(--text_primary);
}

.link {
  color: var(--link-color);
  text-decoration: none;
  cursor: pointer;
  &_bordered {
    border-bottom: 1px solid var(--link-color);
  }
}

.title {
  font-weight: 400;
  &_big {
    font-size: 36px;
    line-height: 24px;
  }
  &_medium {
    font-size: 24px;
    line-height: 28px;
  }
  &_upper-small {
    font-size: 18px;
    line-height: 16px;
  }
  &_small {
    font-size: 14px;
  }
}

.plain-text {
  font-weight: 400;
  &_big {
    font-size: 24px;
    line-height: 28px;
  }
  &_medium {
    font-size: 16px;
    line-height: 20px;
  }
  &_small {
    font-size: 14px;
    line-height: 18px;
  }
}

.text_secondary {
  color: var(--text_secondary);
}

.text_accent {
  color: var(--text_accent);
}

.text_error {
  color: var(--alarm);
}

.text_bold {
  font-weight: 600;
}

.page-title {
  display: inline;
  font-size: 24px;
  font-weight: 500;
  border-bottom: 3px solid var(--link-color);
  padding: 0 0 24px 0;
}

.page-header__wrapper {
  display: block;
  min-height: 54px;
  margin: 0 0 24px 0;
}

.section-title {
  margin: 0 0 24px 0;
  line-height: 100%;
  font-size: 24px;
}


// Buttons

object {
  pointer-events: none;
}

.button {
  display: flex;
  width: 100%;
  height: 50px;
  outline: none;
  border: none;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  cursor: pointer;
  transition: all .125s ease-in;
  .s{
    color: #fff!important;
  }
  &_transparent {
    background: transparent;
  }
  &_primary {
    background: var(--primary);
    color: var(--text_contrast);
  }
  &_round {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  &_bordered {
    border: 1px solid var(--border-color);
    &-top {
      width: 67px;
      height: 67px;
      border-color: var(--primary);
    }
  }
  &_remove {
    width: 22px;
    height: 22px;
    position: relative;
    border-radius: 50%;
    background: #C8C8C8;
    &::before {
      display: block;
      content: '+';
      font-size: 24px;
      color: var(--text_contrast);
      transform: rotate(45deg);
    }
    &:hover {
      background: var(--link-color);
    }
    //&:hover {
    //  &::after {
    //    opacity: 1;
    //    top: 48px;
    //  }
    //}
  }
  &_active {
    background: var(--input-color_active);
    color: var(--text_accent);
  }
}
.remove-label {
  font-size: 12px;
  line-height:  14px;
  text-align: center;
  padding: 8px;
  border-radius: 8px;
  box-shadow: 0 0 12px rgba(0, 0, 0, .25);
  background: #ffffff;
  transform: translateY(-48px) translateX(-8px);
  width: 100px;
  display: block;
}
.button-icon {
  margin: 0 12px 0 0;
}

.breadcrumbs__wrapper {
  width: 100%;
  overflow-x: auto;
}

.breadcrumbs {
  width: 100%;
  padding: 24px 12px 24px 0;
  display: flex;
}

.breadcrumbs__item {
  font-size: 13px;
  line-height: 24px;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  white-space: nowrap;
  &::after {
    content: ' >';
    margin: 0 8px;
    display: block;
  }
  &:last-child {
    &::after {
      display: none;
    }
  }
}

// Inputs
.input {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border: 1px solid var(--border-color);
  background: var(--input-background);
  font-size: 16px;
  color: var(--text_primary);

  &_error {
    border-color: var(--alarm);
  }

  &_textarea {
    resize: none;
    height: 200px;
    padding: 12px;
  }
}

// Radio/Checkboxes
.checkbox-container {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
  padding: 0 0 0 24px;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0px;
  left: 0;
  height: 15px;
  width: 15px;
  background: #fff;
  border: 2px solid var(--border-color);
  border-radius: 0;
}

.checkbox-container:hover input ~ .checkmark {
  background-color: #fff;
}

.checkbox-container input:checked ~ .checkmark {
  background: #fff;
  border-color: var(--checkbox);
  border-radius: 4px;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: block;
  opacity: 0;
  transition: all .125s ease-in-out;
}

.checkbox-container input:checked ~ .checkmark:after {
  opacity: 1;
}

.checkbox-container .checkmark:after {
  left: 3px;
  top: 0;
  width: 3px;
  height: 7px;
  border-bottom: 2px solid var(--checkbox);
  border-right: 2px solid var(--checkbox);
  transform: rotate(45deg);
}

// Table
.table {
  border: none;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}
.table th {
  font-weight: bold;
  border: none;
  text-align: left;
  height: 50px;
  padding: 0 8px;
  font-size: 16px;
}
.table td {
  border: none;
  text-align: left;
  height: 50px;
  padding: 0 8px;
  font-size: 16px;
}
.table tbody tr:nth-child(odd) {
  background: var(--table_odd);
}
.table tbody tr:nth-child(even) {
  background: var(--table_even);
}

// Wrappers
.section {
  padding: 40px 0;
}

.container {
  padding: 0 24px!important;
}

.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  &_row-reverse {
    flex-direction: row-reverse;
  }

  &_column {
    flex-direction: column;

    &-reverse {
      flex-direction: column-reverse;
    }
  }

  &_nowrap {
    flex-wrap: nowrap;
  }

  &_wrap {
    flex-wrap: wrap;
  }

  &_wrap-reverse {
    flex-wrap: wrap-reverse;
  }
}

.justify {
  justify-content: flex-start;

  &_start {
    justify-content: flex-start;
  }

  &_end {
    justify-content: flex-end;
  }

  &_center {
    justify-content: center;
  }

  &_between {
    justify-content: space-between;
  }

  &_around {
    justify-content: space-around;
  }

  &_evenly {
    justify-content: space-evenly;
  }
}

.justify-items {
  justify-items: auto;

  &_start {
    justify-items: start;
  }

  &_end {
    justify-items: end;
  }

  &_center {
    justify-items: center;
  }

  &_stretch {
    justify-items: stretch;
  }
}

.justify-self {
  justify-self: auto;

  &_start {
    justify-self: start;
  }

  &_end {
    justify-self: end;
  }

  &_center {
    justify-self: center;
  }

  &_stretch {
    justify-self: stretch;
  }
}

.content {
  &_center {
    align-content: center;
  }

  &_start {
    align-content: flex-start;
  }

  &_end {
    align-content: flex-end;
  }

  &_between {
    align-content: space-between;
  }

  &_around {
    align-content: space-around;
  }

  &_evenly {
    align-content: space-evenly;
  }
}

.items {
  &_start {
    align-items: flex-start;
  }

  &_end {
    align-items: flex-end;
  }

  &_center {
    align-items: center;
  }

  &_baseline {
    align-items: baseline;
  }

  &_stretch {
    align-items: stretch;
  }
}

.inline {
  display: inline;

  &_block {
    display: inline-block;
  }
}

.hidden {
  display: none;
}

.disabled {
  cursor: not-allowed;
  pointer-events: none;
}

// Footer
.footer__links-wrapper {
  flex-flow: row nowrap;
  justify-content: space-between;
}

.footer__links-list-item {
  margin: 0 0 11px 0;
}

.footer__contact-wrapper {
  padding: 24px 0;
  border-top: 1px solid var(--border-color);
}

.footer__contact-item {
  margin: 0 0 12px 0;
  display: flex;
  align-items: center;
}

.footer__contact-icon {
  margin: 0 8px 0 0;
}

.footer__schedule {
  flex-flow: column nowrap;
}

.footer__schedule-value {
  display: block;
}

.footer__social-list {
  display: flex;
  flex-flow: row nowrap;
}

.footer__social-list-item {
  margin: 0 4px 0 0;
  &:last-child {
    margin: 0;
  }
}

.footer__copy-wrapper {
  padding: 12px;
  background: #F0F0F0;
  position: relative;
  z-index: 100;
  &::before {
    position: absolute;
    left: -24px;
    top: 0;
    height: 100%;
    width: 100vw;
    background: #F0F0F0;
    display: block;
    content: '';
    z-index: 1;
  }
  .footer__copy {
    text-align: center;
    position: relative;
    z-index: 100;
  }
}

.footer__links-list_desktop-visible {
  display: none;
}

.footer__logo-link {
  display: none;
}

.cookies {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 300;
  height: 88px;
  width: 100vw;
  background: #ffffff;
  box-shadow: 0 0 30px rgba(0, 0, 0, .25);
}

.cookies__button {
  width: 50px;
}

.pagination {
  padding: 12px 0;
}

.pagination-list__button {
  border: none;
  background: transparent;
}

.pagination__list-item {
  padding: 0 6px;
}

.pagination__link {
  color: var(--text_primary);
  &_active {
    color: var(--text_secondary);
  }
}

@media screen and (min-width: 768px) {
  .breadcrumbs__item {
    width: auto;
  }
  .page-title {
    font-size: 36px;
    line-height: 24px;
    font-weight: 400;
  }
}

@media screen and (min-width: 1025px) {
  .section-title {
    margin: 0 0 34px 0;
    font-size: 36px;
  }


  .footer__links-list_desktop-hidden {
    display: none;
  }
  .footer__links-list_desktop-visible {
    display: flex;
    flex-flow: column nowrap;
  }
  .footer__links-list {
    width: calc(20% - 12px);
  }
}

@media screen and (min-width: 1366px) {
  .container {
    width: 100%;
    max-width: 1366px;
    margin: 0 auto;
  }
  .footer__wrapper {
    flex-flow: row wrap;
    max-width: 1440px;
    margin: 0 auto;
  }
  .footer__social-wrapper {
    width: 20%;
    order: -1;
    flex-flow: column nowrap;
    justify-content: flex-start;
  }
  .footer__social-list {
    margin: 24px 0;
  }
  .footer__contact-wrapper {
    width: 30%;
    padding: 0 0 0 32px;
    border: none;
  }
  .footer__links-wrapper {
    width: 50%;
  }
  .footer__list-link {
    font-size: 14px;
    &:hover {
      color: var(--text_accent);
    }
  }
  .footer__list-link-b {
    font-size: 16px;
    font-weight: 800;
    &:hover {
      color: var(--text_accent);
    }
  }
  .footer__logo-link {
    display: block;
  }
}

.g-ind{
  margin-right: 5px;
}