@import "./main";
@import "../blocks/modules/header/header";

.auth__icon {
  width: 100%;
  height: auto;
  margin: 24px 0;
}

.input_auth {
  margin: 0 0 12px 0;
  padding: 0 12px 0 42px;
}

.auth-button {
  background: var(--link-color);
  margin: 12px 0;
}

.auth-link {
  margin: 12px 0 0 0;
  color: var(--text_primary);
  text-decoration: underline;
}

.checkmark {
  border: 2px solid var(--link-color);
  border-radius: 4px;
}

.auth-form__input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.auth-form__input-icon {
  position: absolute;
  left: 12px;
  top: 12px;
}

@media screen and (min-width: 768px) {
  .auth__form-wrapper {
    width: 100%;
  }
}

@media screen and (min-width: 1025px) {
  .auth-section {
    padding-top: 0;
  }
  .page-title {
    margin: 0 0 58px 0;
    font-size: 36px;
    font-weight: 400;
  }
  .auth__wrapper {
    width: 100%;
  }
  .auth__icon {
    width: calc(50% - 24px);
  }
  .auth__form-wrapper {
    width: calc(50% - 24px);
  }
}
